"use client";

import { useEffect, useRef, useLayoutEffect } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

const Statement = () => {
  const statementRef = useRef(null);
  const statementSectionRef = useRef(null);
  const textRefs = useRef([]);

  useLayoutEffect(() => {
    gsap.registerPlugin(ScrollTrigger);
    textRefs.current = [];

    const ctx = gsap.context(() => {
      const statement = statementRef.current;
      const words = statement.innerText.split(" ");

      const localScrollTriggers = [];
      statement.innerHTML = "";

      words.forEach((word, index) => {
        const wordSpan = document.createElement("span");
        wordSpan.innerHTML = word + (index < words.length - 1 ? " " : "");
        wordSpan.style.display = "inline-block";
        wordSpan.style.overflow = "hidden";
        textRefs.current.push(wordSpan);
        statement.appendChild(wordSpan);
      });

      textRefs.current.forEach((word, index) => {
        const tween = gsap.fromTo(
          word,
          { y: 50, opacity: 0 },
          {
            y: 0,
            opacity: 1,
            duration: 0.5,
            ease: "power2.out",
            delay: index * 0.05,
            scrollTrigger: {
              trigger: statementRef.current, // âœ… HERE'S THE KEY
              start: "top 80%",
              end: "bottom 90%",
            },
          }
        );

        localScrollTriggers.push(tween.scrollTrigger);
      });
    }, statementSectionRef);

    return () => ctx.revert();
  }, []);

  return (
    <div
      ref={statementSectionRef}
      className="bg-black text-white py-16 px-4 md:px-8 lg:px-16 statement-section w-full "
    >
      <div className="max-w-7xl mx-auto">
        {/* Main statement with animation */}
        <h1
          ref={statementRef}
          className="text-4xl md:text-5xl lg:text-6xl font-light leading-tight tracking-tight mb-12"
        >
          We blend the power of strategy, design, and performance marketing to
          transform founders' visions into remarkable brands. See{" "}
          <a href="#services" className="underline">
            our services
          </a>
          .
        </h1>
      </div>
    </div>
  );
};

export default Statement;
